@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.create__product {
  position: relative;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: calc(100vh - 70px);
  margin: 15px 0;
  margin-top: 30px;
}

.create__product-form {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
}

.create__product-container-leftSection {
  position: relative;
  flex: 0.4 1;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.create__product-leftSection-image-input {
  position: relative;
  width: 50%;
  min-width: 250px;
  min-height: 270px;
  height: 300px;
  background: white;
}

.create__product-image-preview {
  width: 50%;
  min-width: 250px;
  min-height: 270px;

  height: 300px;
  position: relative;
}

.create__product-leftSection-image-input::before {
  content: "+";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: rgb(214, 212, 212);
  display: flex;
  background: #eee;
  border: 4px dashed lightgrey;
  justify-content: center;
  align-items: center;
  font-size: 17rem;
  cursor: pointer;
}

.create__category-input-here {
  font-weight: 800;
  font-size: medium;
  letter-spacing: 1px;
}

.create__product-cancel-image {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -12px;
  right: -12px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: dodgerblue;
  color: white;
  font-weight: 600;
  font-size: larger;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  cursor: pointer;
}

/* ------------- right Section ------------- */

.create__product-container-rightSection {
  margin-top: 30px;
  flex: 0.4 1;
  display: flex;
  flex-direction: column;
}

.create__product-input {
  margin: 8px 10px;
}

.create__product-input input {
  width: 90%;
  padding: 10px 20px;
  outline: none;
  border: 1px solid lightgrey;
  border-radius: 5px;
  font-family: "Montserrat";
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 14px;
}

.create__product-input select {
  width: 90%;
  padding: 10px 20px;
  outline: none;
  border: 1px solid lightgrey;
  border-radius: 2px;
  font-family: "Montserrat";
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 14px;
}

.create__product-form-button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  margin: 10px;
  width: 90%;
  border: none;
  background: dodgerblue;
  color: white;
  font-size: larger;
  letter-spacing: 1.2px;
  font-weight: 600;
  font-family: "Montserrat";
  outline: none;
  cursor: pointer;
  transition: background 0.2s ease;
}

.create__product-form-button:hover {
  background-color: rgb(93, 168, 243);
}

.allproducts {
  width: 100%;
  height: 100%;
  position: relative;
  margin-top: 30px;
}

.allproducts__container {
  display: flex;
  flex-wrap: wrap;
}

.allproducts__product {
  margin: 2px;
  flex: 1 1 30%;
  min-width: calc(270px / 1.08);
  position: relative;
  top: 0;
}

.allproducts__items {
  height: 300px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  margin: 10px 20px;
  box-shadow: 1px 5px 10px lightgray;
}

.allproducts__items__image {
  flex: 0.6 1;
  object-fit: cover;
  overflow: hidden;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.allproducts__items__image:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

.allproducts__items__text {
  flex: 0.4 1;
  z-index: 1;
  padding: 10px;
  line-height: 1.2;
}

.allproducts__items__title {
  font-size: 1.4rem;
  letter-spacing: 1px;
  font-weight: 700;
}

.allproducts__items__description {
  font-size: 0.8rem;
  font-weight: 400;
  letter-spacing: 0.3px;
  font-family: "Montserrat";
  color: grey;
}

.allproducts__items__price-rating {
  position: absolute;
  bottom: 10px;
  left: 10px;
  right: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.allproducts__items__price {
  font-size: x-large;
  font-weight: 700;
}

.allproducts__items__button {
  display: flex;
  padding: 5px 8px;
  letter-spacing: 1px;
  text-transform: capitalize;
  align-items: center;
  justify-content: center;
}

.allproducts__items__button button {
  font-size: 13px;
  padding: 4px 8px;
  border: 1px solid #ddd;
  outline: none;
  transition: all 0.2s linear;
  cursor: pointer;
  letter-spacing: 0.8px;
}

.allproducts__items__button button:hover {
  background: dodgerblue;
  border: 1px solid dodgerblue;
  color: white;
}

.allproducts__edit-delete {
  position: absolute;
  right: 20px;
  top: 20px;
}

.allproducts__edit-delete button {
  border: none;
  outline: none;
  color: white;
  background: dodgerblue;
  padding: 5px;
  margin: 2px;
  cursor: pointer;
}

.allproducts__header {
  position: relative;
  right: 0;
  width: 100%;
  display: flex;
  height: 70px;
  box-shadow: 1px 1px 10px #ddd;
  margin-top: 10px;
}

.allproducts__header-content {
  position: relative;
}

.allproducts__header a {
  color: black;
}

