  -webkit-font-smoothing: antialiased;
}

.success__box {
  padding: 10px 18px;
  background: #5cf352;
  position: fixed;
  top: 30px;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  z-index: 99;
  color: white;
  transition: all 0.4s linear;
  font-size: large;
  min-width: 200px;
  text-align: center;
  text-transform: capitalize;
  font-weight: 600;
  letter-spacing: 2px;
  -webkit-animation: successbox 2s linear;
          animation: successbox 2s linear;
}

.error__box {
  padding: 10px 18px;
  background: rgb(247, 10, 10);
  position: fixed;
  top: 30px;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  z-index: 99;
  color: white;
  transition: all 0.4s linear;
  font-size: large;
  min-width: 200px;
  text-align: center;
  text-transform: capitalize;
  font-weight: 600;
  letter-spacing: 2px;
  -webkit-animation: successbox 2s linear;
          animation: successbox 2s linear;
}

@-webkit-keyframes successbox {
  0% {
    opacity: 0;
  }

  25% {
    opacity: 0.5;
  }

  50% {
    opacity: 1;
  }

  70% {
    opacity: 1;
  }

  75% {
    opacity: 0.5;
  }

  100% {
    opacity: 0;
  }
}

@keyframes successbox {
  0% {
    opacity: 0;
  }

  25% {
    opacity: 0.5;
  }

  50% {
    opacity: 1;
  }

  70% {
    opacity: 1;
  }

  75% {
    opacity: 0.5;
  }

  100% {
    opacity: 0;
  }
}

